}

body {
  background: #2a2a2a;
}
.ag-speedometer {
  /* height: 30.1875em; */
  width: 34.25em;
  background: url(https://t4.ftcdn.net/jpg/01/03/87/17/360_F_103871728_A0whMSIA21juodUU5rDwMZvhBdDXYSIY.jpg) 100% 100% no-repeat;
  -webkit-background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  -o-background-size: 100% 100%;
  background-size: 100% 100%;

  position: relative;
}

 
.speedometerss{
  content: "";
  position: absolute;
  top: 50%;
  left: 40%;
  width: 8.25em;
  height: .1875em;
  background: #FFF;

  -webkit-box-shadow: inset 0 0 .0625em .0625em #F00, 0 0 .3125em .125em #F00;
  -moz-box-shadow: inset 0 0 .0625em .0625em #F00, 0 0 .3125em .125em #F00;
  box-shadow: inset 0 0 .0625em .0625em #F00, 0 0 .3125em .125em #F00;

 

  -webkit-transform-origin: 100% 0;
  -moz-transform-origin: 100% 0;
  -ms-transform-origin: 100% 0;
  -o-transform-origin: 100% 0;
  transform-origin: 100% 0;
 transition: 0.5s;
  transform: translate(-50%, -50%) rotate(285deg);
}

 
